<template>
    <div class="respsow-container">
        <h1>{{$route.meta.title}}</h1>
        <input type="button" value="목록" @click="goList">
        <div class="head-div">
            <ul>
                <li><input type="text" :value="detail.srvMstr.srvMstNm" placeholder="설문지명"  readonly/></li>
                <li><input type="text" :value="detail.srvMstr.srvMstDc" placeholder="설문지설명" readonly/></li>
                <li><input type="text" :value="detail.srvMstr.srvMstTg" placeholder="설문지대상" readonly/></li>
            </ul>
        </div>
        <div class="content-div">
            <h3>요약</h3>
            <RenderSpace v-for="(item, index) in detail.srvMtrItems" :key="index" :item="item"></RenderSpace>
        </div>
    </div>
</template>

<script>
import RenderSpace from '@/components/survey/result/space/RenderSpace.vue'
export default {
  name: 'ResultContainer',
  components: {
    RenderSpace
  },
  data () {
    return {
      detail: {
        srvMstr: {
          srvMstNm: '',
          srvMstDc: '',
          srvMstTg: ''
        },
        srvMtrItems: {}
      }
    }
  },
  methods: {
    goList () {
      this.$router.push({name: 'list'})
    }
  },
  created () {
    this.axios.post('/action/response/showStt', {srvMstrId: this.$route.params.srvMstrId}).then((response) => {
      const data = response.data
      this.detail.srvMstr.srvMstNm = data.srvMstr.srvMstNm
      this.detail.srvMstr.srvMstDc = data.srvMstr.srvMstDc
      this.detail.srvMstr.srvMstTg = data.srvMstr.srvMstTg
      this.detail.srvMtrItems = data.srvMtrItems
    })
  }
}
</script>

<style scoped>
  .respsow-container {
    padding: 10px;
    width: 45em;
    height: 100%;
    margin: auto;
  }
  .respsow-container div.head-div {
    margin: 30px 0px 30px 0px;
    width: 40em;
    border: 1px solid;
  }
  .respsow-container div.content-div {
    margin: 30px 0px 30px 0px;
    width: 40em;
  }
  .respsow-container div ul {
    list-style: none;
    padding: 5px;
  }
  .respsow-container div ul li {
    padding: 10px;
  }

  .respsow-container div ul li input {
    border-top: 0;
    border-left: 0;
    border-right: 0;
    border-bottom: 1px solid;
    padding: 10px;
    width: 30em;
  }
</style>



