<script>
import {HorizontalBar} from 'vue-chartjs'
export default {
  name: 'BarGraph',
  extends: HorizontalBar,
  props: ['item'],
  data () {
    return {
      data: {
        labels: [],
        datasets: [
          {
            label: '',
            backgroundColor: '#f87979',
            data: []
          }
        ]
      },
      options: {
        responsive: false,
        maintainAspectRatio: false
      }
    }
  },
  created () {
    const array = this.$props.item.sttObjData
    let value = []
    array.forEach(item => {
      this.data.labels.push(item.srvItemNm)
      value.push(item.srvRespnsttCnt)
    })
    this.data.datasets[0].data = value
    this.data.datasets[0].label = this.$props.item.srvMtritemsTitle
  },
  mounted () {
    this.renderChart(this.data, this.options)
  }
}
</script>



