<template>
    <div class="render-space">
       <div class="render-graph">
           <component :is="component" :item="$props.item"></component>
       </div>
    </div>
</template>

<script>
import BarGraph from '@/components/survey/result/space/BarGraph'
export default {
  name: 'RenderSpace',
  props: ['item'],
  components: {
    BarGraph
  },
  data () {
    return {
      component: ''
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      // const type = String(this.item.srvMtritemsType)
      this.component = BarGraph
    }
  }
}
</script>

<style scoped>
    .render-space {
        border: 1px solid;
        width: 40em;
        height: auto;
        margin: 10px 0px 10px 0px;
    }
    .render-space h4 {
        margin: 20px;
    }
    .render-space > .render-graph {
        border: 1px solid;
        width: 37em;
        height: auto;
        margin: 20px;
    }
</style>



